<template>
  <div class="home">
    <Search></Search>
    <div class="shade"></div>
    <div class="top">
      <div>
        <!-- <img src="../assets/images/icon/returnKey.png" alt="" /> -->
        <span>排行榜</span>
      </div>
    </div>
    <div class="item">
      <Loading v-if="isLoading"></Loading>
      <ul>
        <router-link
          tag="li"
          v-for="(item, index) in data"
          :key="index"
          :to="`/particulars/${item.id}`"
        >
          <img :src="item.coverImgUrl" alt="" />
          <div class="right">
            <p>{{ item.description }}</p>
          </div>
          <span>{{ item.updateFrequency }}</span>
        </router-link>
      </ul>
    </div>
    <BottomNav></BottomNav>
  </div>
</template>
<style lang="less" scoped>
.home {
  background-color: white;
  .shade {
    height: 10px;
  }
  .search {
    top: 0;
  }
  .top {
    padding: 0 3%;

    div {
      // float: left;
      text-align: left;
      // img {
      //   float: left;
      //   margin-right: 10px;
      // }
      span {
        display: block;
        text-align: left;
        padding: 4px 10px;
        font-weight: 400;
        font-size: 1.2rem;
      }
    }
  }
  .item {
    ul {
      padding: 0 3%;
      box-sizing: border-box;
      padding-bottom: 150px;

      li {
        width: 100%;
        height: 16vh;
        border-radius: 16px;
        overflow: hidden;
        margin-top: 12px;
        position: relative;
        &:nth-child(odd) {
          background-color: #fdebf1;
        }
        &:nth-child(even) {
          background-color: #d6fbf8;
        }
        span {
          position: absolute;
          top: 10px;
          right: 10px;
          font-size: 0.6rem;
          text-align: right;
          color: rgb(165, 165, 165);
        }
        img {
          height: 80%;
          position: absolute;
          top: 50%;
          left: 10px;
          transform: translateY(-50%);
          border-radius: 10px;
        }
        .right {
          width: 50%;
          position: absolute;
          top: 50%;
          left: 130px;
          transform: translateY(-50%);

          p {
            height: 60px;
            text-align: left;
            font-size: 0.9rem;
            color: rgb(65, 65, 65);
            overflow: hidden;
          }
        }
      }
    }
  }
}
</style>
<script>
import BottomNav from "@/components/BottomNav";
import Search from "@/components/home/Search";
import Loading from "@/components/Loading";

export default {
  name: "Personal",
  components: {
    BottomNav,
    Search,Loading
  },
  data() {
    return {
      data: [],
      isLoading:true
    };
  },
  created() {
    this.$axios
      .get("/toplist")
      .then((data) => {
        return data.data.list;
      })
      .then((item) => {
        // console.log(item);
        this.data = item;
        this.isLoading=false;
      });
  },

  beforeRouteEnter(to, from, next) {
    to, from;
    next((vm) => {
      vm.$emit("flag", true);
    });
  },
  beforeRouteLeave(to, from, next) {
    to, from;
    this.$emit("flag", false);
    next();
  },
};
</script>
